body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #3F4A5A;
  color: rgb(228, 228, 228);
}

header {
  width: 100%;
  margin: 4vh .5%;
}

footer {
  position: relative;
}

main {
  display: flex;
  width: 100%;
}

.page {
  text-align: justify;
  width: 80%;
}
.App-logo {
    width: 10%;
    animation: App-logo-spin infinite 20s linear;
    pointer-events: none;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  .head {
      display: flex;
  }

  .head h1 {
      width: 90%;
      font-size: 3em;
      text-align: center;
      font-family: 'Akaya Telivigala', cursive;
  }
  
  
.menu {
    width: 20%;
}

.menu > ul {
    list-style-type: none;
}

.menu > ul > li > a {
    display: block;
    color: rgb(228, 228, 228);
    letter-spacing: 2.5px;
    text-transform: uppercase;
    text-decoration: none;
    border-left: 10px solid;
    margin: 0 6%;
    font-size: 20px;
    transition: .4s;
}

.menu > ul > li {
    margin-bottom: 15%;
}

.menu ul li a:hover {
    transform: translateX(20px);
}

.menu ul li a.active {
    transform: translateX(0)
}


.menu > ul > li:nth-child(1) > a.active {
    border-color: rgba(115, 226, 245, 1);
}

.menu > ul > li:nth-child(2) > a.active {
    border-color: rgba(73, 184, 66, 1);
}

.menu > ul > li:nth-child(3) > a.active {
    border-color: rgba(128, 24, 197, 1);
}

.menu > ul > li:nth-child(4) > a.active {
    border-color: rgba(235, 39, 26, 1);
}

.menu > ul > li:nth-child(5) > a.active {
    border-color: rgba(219, 203, 12, 1);
}

.description {
    padding: 0 100px;
}

.description h1 {
    padding-bottom: 20px;
}

.description article {
    text-indent: 15px;
}
.foot {
    position: fixed;
    bottom: 0;
    background-color: black;
    width: 100%;
    text-align: center;
    padding: 6px 0;
}

